const NftDetail = ({ nftRecord, phase1ZbcLockAmount }) => {
  const imgPath = `/static/images/NFT/${
    nftRecord.nftType === NFT_TYPE.ZEPOCH ? 'zepoch' : 'themis'
  }.png`;

  const lockedTime =
    nftRecord.phase === 1 &&
    new Date(nftRecord.phase1ZbcUserLocked.timestamp * 1000).toISOString().substring(0, 10);

  return (
    <div className={styles.NftDetail}>
      <div className={styles.tagWrap}>
        <div className={classNames(commonStyles.gradientTitle, styles.phaseTag)}>
          Phase {nftRecord.phase}
        </div>
        {nftRecord.staked && <div className={styles.stakeTag}>Staked</div>}
      </div>
      <img src={imgPath} alt="node" />
      <div className={styles.contentWrap}>
        <h2 className={classNames(styles.nftId, commonStyles.gradientTitle)}>{`#${formatNumber(
          nftRecord.tokenId,
          '000',
        )}`}</h2>
        <h1 className={classNames(styles.title, commonStyles.gradientTitle)}>
          {nftRecord.nftType}
        </h1>
        {nftRecord.phase === 1 && (
          <div className={styles.infoItemGroup}>
            {nftRecord.phase1ZbcUserLocked.unlocked && (
              <div className={styles.infoItem}>
                <h2>Status</h2>
                <p>Unlocked</p>
              </div>
            )}
            {!nftRecord.phase1ZbcUserLocked.unlocked && !nftRecord.phase1ZbcUserLocked.unlockable && (
              <div className={styles.infoItem}>
                <h2>Unlock in</h2>
                <Countdown
                  value={nftRecord.phase1ZbcUserLocked.unlockTimestamp * 1000}
                  format="D:HH:mm:ss"
                />
              </div>
            )}
            {!nftRecord.phase1ZbcUserLocked.unlocked && nftRecord.phase1ZbcUserLocked.unlockable && (
              <div className={styles.infoItem}>
                <h2>Status</h2>
                <p>Unlockable</p>
              </div>
            )}

            <div className={styles.infoItem}>
              <h2>ZBC Amount</h2>
              <p>{formatNumber(phase1ZbcLockAmount)}</p>
            </div>
            <div className={styles.infoItem}>
              <h2>Locked At</h2>
              <p>{lockedTime}</p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

