import { ethers } from 'ethers';
import { useState, useCallback } from 'react';

import axios from '../utils/axios';

export function usePoints() {
  const [rankList, setTankList] = useState([]);

  const [myRanking, setMyRanking] = useState({});

  const getPointsRanking = useCallback(() => {
    axios
      .get('manage/points/now')
      .then((res) => {
        setTankList(res.data.data?.list || []);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  const getMyRanking = useCallback((address) => {
    if (!ethers.utils.isAddress(address)) {
      return;
    }
    axios
      .get(`manage/points/ranking/${address}`)
      .then((res) => {
        setMyRanking(res.data.data || {});
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  return {
    rankList,
    myRanking,
    setTankList,
    getPointsRanking,
    getMyRanking,
  };
}
