import React, { useLayoutEffect, useState, useEffect } from 'react';
import { Row, Col } from 'antd';
import classNames from 'classnames';

import MainContainer from '../../components/UI/MainContainer/MainContainer';
import commonStyles from '../../common.module.scss';
import TitleCombo from '../../components/UI/TitleCombo/TitleCombo';
import CustomButton from '../../components/UI/CustomButton/CustomButton';
import { useIsMobileView } from '../../hooks/useIsMobileView';
import ProgressiveVideo from '../../components/UI/ProgressiveVideo/ProgressiveVideo';
import { useCalcPhase2Cost, usePhase1Config } from '../../hooks/useManager';
import { useZepochNodeTotalSupply, useZPointTotalSupply } from '../../hooks/useToken';
import { formatNumber } from '../../utils/utils';
import { useCurrentPhase } from '../../hooks/useCurrentPhase';
import NavLinkWithQuery from '../../components/NavLinkWithQuery/NavLinkWithQuery';

import styles from './About.module.scss';

const STATS_BREAK_POINTS = {
  PHASE1: { md: 8 },
  PHASE2: { md: 12, lg: 12, xl: 6 },
};

const About = () => {
  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const isMobile = useIsMobileView();
  const { data: phase1Config } = usePhase1Config();
  const { data: zPointTotalSupply } = useZPointTotalSupply();
  const { data: zepochNodeTotalSupply } = useZepochNodeTotalSupply();
  const { data: phase2Cost } = useCalcPhase2Cost(1);
  const currentPhase = useCurrentPhase();
  const [stats, setStats] = useState([]);
  const breakPoints = currentPhase === 1 ? STATS_BREAK_POINTS.PHASE1 : STATS_BREAK_POINTS.PHASE2;

  useEffect(() => {
    if (currentPhase === 1) {
      setStats([
        {
          id: 0,
          text: 'Zepoch Registered',
          img: 'Node',
          value: formatNumber(zepochNodeTotalSupply),
        },
        {
          id: 1,
          text: 'ZBC Staked',
          img: 'Zebec',
          value: formatNumber(phase1Config?.phase1ZbcTotalLocked),
        },
        {
          id: 2,
          text: 'Zebec Points',
          img: 'Points',
          value: formatNumber(zPointTotalSupply),
        },
      ]);
    }
    if (currentPhase === 2) {
      setStats([
        {
          id: 0,
          text: 'Zepoch Registered',
          img: 'Node',
          value: formatNumber(zepochNodeTotalSupply),
        },
        { id: 1, text: 'Zepoch Price', img: 'Price', value: formatNumber(phase2Cost, '$0,0[.]00') },
        {
          id: 2,
          text: 'Zebec Points',
          img: 'Points',
          value: formatNumber(zPointTotalSupply),
        },
        { id: 3, text: 'Horizon Treasury', img: 'Treasury', value: formatNumber(888, '$0,0[.]00') },
      ]);
    }
  }, [
    currentPhase,
    zepochNodeTotalSupply,
    phase1Config?.phase1ZbcTotalLocked,
    zPointTotalSupply,
    phase2Cost,
  ]);

  return (
    <div className={styles.About}>
      <div className={styles.banner} data-aos="fade-up">
        <ProgressiveVideo
          imgSrc="/static/images/About/bannerBg.jpg"
          videoSrc="/static/images/About/bannerBg.mp4"
        />
        <MainContainer className={styles.bannerTextWrap}>
          <TitleCombo>
            <h2>Zebec Operation Horizon</h2>
            <p>
              Operation Horizon is creating a payment-focused Layer 1 service that is independent,
              highly efficient, and safe. It will be built and sustained by the community - people
              just like you.
            </p>
            <p>
              The Zepoch Node lets you earn rewards and exclusive privileges from the ecosystem.
            </p>
          </TitleCombo>
          <NavLinkWithQuery to="/participate">
            <CustomButton>Get Zepoch Now</CustomButton>
          </NavLinkWithQuery>
        </MainContainer>
      </div>
      <div
        className={styles.mainContent}
        style={{ backgroundImage: 'url(/static/images/About/bg.jpg)' }}>
        <MainContainer className={styles.mainContainer}>
          <div className={styles.getNodeWrap} data-aos="fade-up">
            <TitleCombo className={styles.getNodeCombo} small>
              <h2>Participate in Operation Horizon</h2>
              <p>
                Participate in Operation Horizon to help us expand the Zebec ecosystem while earning
                both token reward and exclusive privileges.
              </p>
            </TitleCombo>
            <div className={styles.buttonWrap}>
              <NavLinkWithQuery to="/participate">
                <CustomButton className={styles.getNftButton}>Get Zepoch Now</CustomButton>
              </NavLinkWithQuery>
              <NavLinkWithQuery to="/learnMore">
                <CustomButton className={styles.getNftButton} secondary>
                  Learn more
                </CustomButton>
              </NavLinkWithQuery>
            </div>
            <div className={styles.statsWrap}>
              <Row gutter={[16, 16]} justify="space-around">
                {stats.map((item) => (
                  <Col
                    span={24}
                    // eslint-disable-next-line
                    {...breakPoints}
                    key={item.id}>
                    <div className={styles.statsItem}>
                      <div className={styles.statsIconWrap}>
                        <img src={`/static/images/About/statsIcon${item.img}.svg`} alt="icon" />
                      </div>
                      <div className={styles.statsTextWrap}>
                        <h2 className={commonStyles.gradientTitle}>{item.text}</h2>
                        <p>{item.value}</p>
                      </div>
                    </div>
                  </Col>
                ))}
              </Row>
            </div>
          </div>
          <div className={styles.nftIntro}>
            <TitleCombo small>
              <h2>Key Components</h2>
            </TitleCombo>
            <div className={styles.nftIntroItem}>
              <video
                autoPlay
                muted
                loop
                playsInline
                poster="/static/images/NFT/zepoch.png"
                data-aos="fade-right">
                <source src="/static/images/NFT/zepoch.mp4" type="video/mp4" />
              </video>
              <div className={styles.nftIntroAction} data-aos="fade-left">
                <h2>ZBC Zepoch Node NFT</h2>
                <p>
                  The Zepoch Node NFT is an exclusive Proof of Identity for those running validator
                  nodes on the Zebec Public Chain, which launches soon.
                </p>
                <NavLinkWithQuery to="/participate">
                  <CustomButton>Become a Validator</CustomButton>
                </NavLinkWithQuery>
              </div>
            </div>
            <div className={classNames(styles.nftIntroItem, styles.second)}>
              <div
                className={classNames(styles.nftIntroAction, styles.second)}
                data-aos="fade-right">
                <h2>ZBC Themis Gavel NFT</h2>
                <p>
                  Themis Gavel is a soul-bound NFT that uses the EIP-5114 protocol and is designed
                  for Zebec DAO governance and ecosystem governance.
                </p>
                <NavLinkWithQuery to="/participate">
                  <CustomButton>Hodl a Gavel</CustomButton>
                </NavLinkWithQuery>
              </div>

              <video
                autoPlay
                muted
                loop
                playsInline
                poster="/static/images/NFT/themis.png"
                data-aos="fade-left">
                <source src="/static/images/NFT/themis.mp4" type="video/mp4" />
              </video>
            </div>
          </div>
        </MainContainer>

        <div
          className={styles.pointsSystemWrap}
          style={{ backgroundImage: 'url(/static/images/About/pointsSystemBg.png)' }}>
          <MainContainer className={styles.pointsSystemContainer}>
            <TitleCombo small>
              <h2 data-aos="fade-up">Zebec Points System</h2>
              <p data-aos="fade-up">
                Zebec Points are an incentive system to incubate the ecosystem. They reward you for
                staking, giving referrals, building ecosystem projects, and participating in Zebec
                agreements and special missions. Earn Zebec Points for extra income.
              </p>
            </TitleCombo>
            <NavLinkWithQuery to="/points">
              <CustomButton>Join Now</CustomButton>
            </NavLinkWithQuery>
          </MainContainer>
        </div>
        <MainContainer className={styles.saleWrap}>
          {!isMobile && (
            <img
              className={styles.saleWatermark}
              src="/static/images/About/saleWatermark.svg"
              alt="zepoch"
            />
          )}
          {isMobile && (
            <img
              className={styles.saleWatermark}
              src="/static/images/About/saleWatermarkMobile.svg"
              alt="zepoch"
            />
          )}
          <div className={styles.saleTextWrap}>
            <img src="/static/images/About/logo.svg" alt="logo" />
            <div className={styles.vbar} />

            {!isMobile && (
              <img
                className={styles.saleText}
                src="/static/images/About/nodeOnSale.svg"
                alt="node"
              />
            )}
            {isMobile && (
              <img
                className={styles.saleText}
                src="/static/images/About/nodeOnSaleMobile.svg"
                alt="node"
              />
            )}
          </div>
          <NavLinkWithQuery to="/participate">
            <CustomButton className={styles.buyButton} rounded>
              Get Zepoch Now
            </CustomButton>
          </NavLinkWithQuery>
        </MainContainer>
      </div>
    </div>
  );
};

export default About;
